.button {
  width: 100%;
  margin: 16px 0 24px 0;
  padding: 10px 0;
  font-size: 18px;
  border: 1px solid #bbb;
  border-radius: 2px;
  background: #f8f8f8;
  cursor: pointer;
  transition: background 0.2s;
}
.button:disabled {
  background: #eee;
  color: #aaa;
  cursor: not-allowed;
} 